import { create, tsx } from "@dojo/framework/core/vdom";
import Link from "@dojo/framework/routing/Link";
import Card from "@dojo/widgets/card";
import store from "../../middleware/store";

import * as css from "./MapList.m.css";

const cardStyling = {
    "@dojo/widgets/card": {
        root: [css.card],
    },
};

const factory = create({ store }).properties();

export default factory(function MapList({ middleware: { store } }) {
    const { get, path } = store;
    const mapList = get(path("maps", "list"));
    return (
        <div classes={[css.root]}>
            {mapList &&
                mapList.map((map) => (
                    <Link key={map.id} classes={[css.item]} to="map" params={{ mapId: map.id }}>
                        <Card
                            classes={cardStyling}
                            title={map.name}
                            subtitle={`${map.worldType} - ${map.width}x${map.height}`}
                        />
                    </Link>
                ))}
        </div>
    );
});
